<template>
  <div id="app">
    <div class="header"
      :class="{'state1':state==1,'state2':state==2,'size1':size==1,'size2':size==2}"
      ref='header'>
      <div class="header-main">
        <div class="logo">
          <a href='./index.html#/baseHome'>
            <img src='./images/logo1.png' v-if="state==1">
            <img src='./images/logo2.png' v-if="state==2">
          </a>
        </div>
        <div class="navbar">
          <ul>
            <li><a href='./index.html#/baseDynamic'>最新动态</a></li>
            <li><a href='./index.html#/baseKnowledge'>节能知识</a></li>
            <li><a href='./index.html#/basePolicy'>政策支持</a></li>
            <li><a href='./index.html#/baseNotice'>通知</a></li>
          </ul>
        </div>
      </div>
    </div>
    <div class="main">
      <h1 class="title">{{title}}</h1>
      <div class="content" v-html='content'></div>
    </div>
  </div>
</template>

<script>
export default {
  data:function(){
    var id = this.$route.params.id;
    console.log(id);
    return {
      path: '',
      state: 2,
      size:1,
      id:id,
      title:'',
      content:''
    }
  },
  methods:{
    getDataApi:function(){
      var _this = this;
      // 生成和配置post信息
      var formData = new FormData();
      formData.append("type","C1003");
      formData.append("pushid",this.id);
      var config = {
        headers:{
          'Content-Type':'application/x-www-from-urlencoded'
        }
      }

      this.$ajax.post('http://121.42.203.85/energycenter/public/api/home/showDetailPush',formData,config)
      .then(function(response){
        console.log(response.data);
        _this.title = response.data.msg.push.title;
        _this.content = response.data.msg.push.content;
      })
    },
    changeSize:function(){
      var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
      if(scrollTop>50){
        this.size = 2
        return
      }

      this.size = 1;
    }
  },
  created:function(){
    this.getDataApi();
    // 绑定滚动事件
    window.addEventListener('scroll',this.changeSize);

  }
}
</script>

<style scoped lang='less'>
  @media screen and (min-width: 1300px){
    .header-main{
      width: 1170px;
    }
    .main{
      width: 1170px;
    }
  }
  @media screen and (max-width: 1299px){
    .header-main{
      width: 960px;
    }
    .main{
      width: 960px;
    }
  }
  .header{
    width: 100%;
    box-shadow: 1px 0px 1px rgba(0,0,0,0.2);
    .header-main{
      height: 100%;
      margin:auto;
      transition: all 0.2s linear;
      .logo{
        float: left;
        img{
          transition: all 0.2s;
        }
      }
      .navbar{
        float: right;
        ul{
          li{
            display: inline-block;
            a{
              width:90px;
              margin-left: 13px;
              margin-right: 13px;
              display: inline-block;
              font-size: 16px;
              text-align: center;
              transition: all 0.2s linear;
            }
          }
        }
      }
    }
  }
  .state1{
    background-color: #6DA685;
    ul{
      li{
        a{
          color:#fff;
          border-bottom: 5px solid #6DA685;
        }
        .router-link-active{
          border-bottom: 5px solid #fff;
        }
      }
      li:hover a{
        border-bottom: 5px solid #fff;
      }
    }
  }
  .state2{
    background-color: #fff;
    ul{
      li{
        a{
          color:#333;
          border-bottom: 5px solid #fff;
        }
        .router-link-active{
          border-bottom: 5px solid #6DA685;
        }
      }
      li:hover a{
        border-bottom: 5px solid #6DA685;
      }
    }
  }
  .size1{
    height: 80px;
    .logo{
      margin-top: 20px;
      img{
        width:176px;
        height: 41px;
      }
    }
    .navbar{
      float: right;
      ul{
        li{
          a{
            height: 75px;
            line-height: 75px;
          }
        }
      }
    }
  }
  .size2{
    height: 60px;
    .logo{
      margin-top: 10px;
      img{
        transform: scale(0.9);
      }
    }
    .navbar{
      float: right;
      ul{
        li{
          a{
            height: 55px;
            line-height: 55px;
          }
        }
      }
    }
  }
  .main{
    margin:auto;
    .title{
      text-align: center;
        font-weight: 700;
        font-size: 32px;
        margin-bottom: 50px;
        margin-top: 50px;
    }
    .content{
      line-height: 39px;
      p{
        font-size: 24px;
        color:red;
      }
      h1{font-size: 34px;}
      h2{font-size: 32px;}
      h3{font-size: 30px;}
      h4{font-size: 28px;}
      h5{font-size: 26px;}
      /* table 样式 */
      table {
        border-top: 1px solid #ccc;
        border-left: 1px solid #ccc;
      }
      table td,
      table th {
        border-bottom: 1px solid #ccc;
        border-right: 1px solid #ccc;
        padding: 3px 5px;
      }
      table th {
        border-bottom: 2px solid #ccc;
        text-align: center;
      }

      /* blockquote 样式 */
      blockquote {
        display: block;
        border-left: 8px solid #d0e5f2;
        padding: 5px 10px;
        margin: 10px 0;
        line-height: 1.4;
        font-size: 100%;
        background-color: #f1f1f1;
      }

      /* code 样式 */
      code {
        display: inline-block;
        *display: inline;
        *zoom: 1;
        background-color: #f1f1f1;
        border-radius: 3px;
        padding: 3px 5px;
        margin: 0 3px;
      }
      pre code {
        display: block;
      }
    }
  }
</style>



