import Vue from 'vue'
import VueRouter from 'vue-router'
import axios from 'axios'
import App from './App'
import vText from '../../components/v-text/v-text.vue'

Vue.prototype.$ajax = axios;
Vue.use(VueRouter);
const routes = [
	{path:'/:type/:id',component:vText}
]

const router = new VueRouter({
	routes
});
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router:router
})


